import { Component, Inject, OnInit } from '@angular/core';
import { ICliente } from '../../models/cliente';
import { ClientesService } from '../../service/clientes.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
    selector: 'app-save-clientes',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss']
})
export class SaveComponent implements OnInit {
    columns = ['nombre', 'activo', 'acciones'];
    form: FormGroup;
    cliente: ICliente;
    title: string;
    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _clientesService: ClientesService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>
    ) {
        this.cliente = this.data ? this.data : null;
        this.title = this.cliente?.codigo ? 'Editar cliente' : 'Nuevo cliente';
    }

    ngOnInit(): void {
        this.newForm();
    }

    newForm() {
        this.form = new FormGroup({
            nombre: new FormControl((this.cliente ? this.cliente.nombre : ''), Validators.required),
            apellido: new FormControl((this.cliente ? this.cliente.apellido : ''), Validators.required),
        });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            this.cliente = {
                ...this.cliente,
                nombre: this.form.get('nombre').value,
                apellido: this.form.get('apellido').value,
            }
            if (this.cliente.codigo) {
                this._clientesService.updateCliente(this.cliente)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            } else {
                this._clientesService.saveCliente(this.cliente)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close()
    }
}